.statictics {
  width: 360px;
}

.statictics__img {
  width: 360px;
  height: 208px;
  margin-bottom: 37px;
}

.statictics__subtitle {
 @include subtitle;
 text-align: center;
 margin-bottom: 41px;
}

.statictics__table {
  border-collapse: collapse;
  width: 100%;
}

.statictics__data {
  width: calc(100%/3);
  @include font(45px, 45px, 700);
  padding-bottom: 19px;

  & span {
    display: inline-block;
    transform: scale(0.5);
    position: relative;
    top: -9px;
    left: -7px;
  }
}

.statictics__text {
  @include text-little;
}
